.decrease-btn{

    border: .6px solid #c0c0c0;
   min-width: 2.6vw;
    border-top-left-radius: .15rem;
    border-bottom-left-radius: .15rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.increase-btn{

    border: .6px solid #c0c0c0;
    min-width: 2.6vw;
    border-top-right-radius: .15rem;
    border-bottom-right-radius: .15rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.value-area{

   
    min-width: 7vh;
    border-top: .6px solid #c0c0c0;
    border-bottom: .6px solid #c0c0c0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.table-strucure-container{
 
   
    border-radius: .5rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border: .1rem solid #fa5390;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


    
}

.table-container{
    background-color: white;
    border-radius: 1rem;
    padding: 1rem;
}

.row1{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.row2{
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.round-circle-available {
    border-radius: 100px;
    height:3vh;
    width: 3vh;
    background-color: #B1E2A5;
    
}

.round-circle-selected {
    border-radius: 100px;
    height:3vh;
    width: 3vh;
    background-color: #F78DA7;
    
}

.round-circle-soled {
    border-radius: 100px;
    height:3vh;
    width: 3vh;
    background-color: #AFC3E5;
    
}





